import React, { Component } from 'react'
import './App.scss';
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';
import Popper from 'popper.js';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import 'jquery/dist/jquery';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faUserCircle, faHome, faBook, faVideo, faEnvelope, faMicrophone, faCog, faQuestion, faCamera } from '@fortawesome/free-solid-svg-icons'
import { wait } from '@testing-library/react';


import {BrowserView, MobileView} from 'react-device-detect'

export default class App extends Component {

  componentWillMount(){
    this.state = {
      partner : "Pursuit Volley",
      curPage : "home",
      pageTitle: "Home",
      showSidebarWords: "hidden"
    }

    this.getCurrentPage = this.getCurrentPage.bind(this);
    this.showFullSidebar = this.showFullSidebar.bind(this);
    this.shrinkSidebar = this.shrinkSidebar.bind(this);

  }


  switchWindows(page){
    switch (page) {
      case "home":
        this.setState({
          curPage: "home"
        })
        break;
      case "profile":
        this.setState({
          curPage: "profile"
        })
        break;
    
      case "library":
        this.setState({
          curPage: "library"
        })
        break;
          
      case "videos":
        this.setState({
          curPage: "videos"
        })      
        break;
          
      case "messages":
        this.setState({
          curPage: "messages"
        })
        break;
          
      case "podcasts":
        this.setState({
          curPage: "podcasts"
        })
        break;
      case "settings":
        this.setState({
          curPage: "settings"
        })
        break;
      case "about":
        this.setState({
          curPage: "about"
        })
        break;
      default:
        break;
    }
  }


  getCurrentPage(){
    switch (this.state.curPage) {
      case "home":
        return <h2>home content</h2>
      case "profile":
        return <h2>profile content</h2>
      case "library":
        return <h2>library content</h2>
      case "videos":
        return <h2>videos content</h2>
      case "messages":
        return <h2>messages content</h2>
      case "podcasts":
        return <h2>podcasts content</h2>
      case "settings":
        return <h2>settings content</h2>
      case "podcasts":
        return <h2>about content</h2>

      default:
        break;
    }
  }

  showFullSidebar(){
    setTimeout(() => {  
      this.setState({
        showSidebarWords: ""
      });
     }, 200);
    
  }

  shrinkSidebar(){
    this.setState({
      showSidebarWords: "hidden"
    });
  }

  render() {
    return (
      <div className="app">
        <BrowserView>
          <div className="row content">
            <div className="sidebar" onMouseEnter={this.showFullSidebar} onMouseLeave={this.shrinkSidebar}>
              <div onClick={()=>this.switchWindows("home")} className="custom-nav-item"> <img id="sidebar-logo" src="https://placehold.it/500x500"></img><span className={this.state.showSidebarWords}></span> </div>
              <div className="nav-separator"></div>
              <div onClick={()=>this.switchWindows("profile")} className={ (this.state.curPage == "profile" ? "active-nav " : "" ) + "custom-nav-item"}><FontAwesomeIcon className="sidebar-icon" icon={faUserCircle}></FontAwesomeIcon><span className={this.state.showSidebarWords}> Josh Mullaney</span></div>
              <div onClick={()=>this.switchWindows("home")} className={ (this.state.curPage == "home" ? "active-nav " : "" ) + "custom-nav-item"}><FontAwesomeIcon className="sidebar-icon" icon={faHome}></FontAwesomeIcon><span className={this.state.showSidebarWords}> Home</span></div>
              <div onClick={()=>this.switchWindows("library")} className={ (this.state.curPage == "library" ? "active-nav " : "" ) + "custom-nav-item"}><FontAwesomeIcon className="sidebar-icon" icon={faBook}></FontAwesomeIcon><span className={this.state.showSidebarWords}> Library</span></div>
              <div onClick={()=>this.switchWindows("videos")} className={ (this.state.curPage == "videos" ? "active-nav " : "" ) + "custom-nav-item"}><FontAwesomeIcon className="sidebar-icon" icon={faVideo}></FontAwesomeIcon><span className={this.state.showSidebarWords}> Your Videos</span></div>
              <div onClick={()=>this.switchWindows("messages")} className={ (this.state.curPage == "messages" ? "active-nav " : "" ) + "custom-nav-item"}><FontAwesomeIcon className="sidebar-icon" icon={faEnvelope}></FontAwesomeIcon><span className={this.state.showSidebarWords}> Messages</span></div>
              <div onClick={()=>this.switchWindows("podcasts")} className={ (this.state.curPage == "podcasts" ? "active-nav " : "" ) + "custom-nav-item"}><FontAwesomeIcon className="sidebar-icon" icon={faMicrophone}></FontAwesomeIcon><span className={this.state.showSidebarWords}> Podcasts</span></div>
              <div className="nav-separator"></div>
              <div onClick={()=>this.switchWindows("settings")} className={ (this.state.curPage == "settings" ? "active-nav " : "" ) + "custom-nav-item"}><FontAwesomeIcon className="sidebar-icon" icon={faCog}></FontAwesomeIcon><span className={this.state.showSidebarWords}> Settings</span></div>
              <div onClick={()=>this.switchWindows("about")} className={ (this.state.curPage == "about" ? "active-nav " : "" ) + "custom-nav-item"}><FontAwesomeIcon className="sidebar-icon" icon={faQuestion}></FontAwesomeIcon><span className={this.state.showSidebarWords}> About Us</span></div>
            </div>
            <div className="title-wrapper">
              <h2 className="current-title">OneFuture | Powered by PursuitVolley</h2> 
            </div>
            <div className="container">
              <div className="page-content">
                {this.getCurrentPage()}
              </div>
            </div>
          </div>
        </BrowserView>
        <MobileView>
          <nav class="navbar navbar-expand-lg navbar-dark mobile-menu-wrapper">
            <a class="navbar-brand force-foreground" href="#">OneFuture x PursuitVolley</a>
            <button class="navbar-toggler force-foreground" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
              <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse force-foreground" id="navbarNav">
              <ul class="navbar-nav">
                <li class="nav-item">
                  <div onClick={()=>this.switchWindows("profile")} className={ (this.state.curPage == "profile" ? "active-nav " : "" ) + "custom-mobile-nav-item" }><FontAwesomeIcon className="sidebar-icon" icon={faUserCircle}></FontAwesomeIcon> Josh Mullaney</div>
                </li>
                <li class="nav-item">
                  <div onClick={()=>this.switchWindows("home")} className={ (this.state.curPage == "home" ? "active-nav " : "" ) + "custom-mobile-nav-item" }><FontAwesomeIcon className="sidebar-icon" icon={faHome}></FontAwesomeIcon> Home</div>
                </li>
                <li class="nav-item">
                  <div onClick={()=>this.switchWindows("library")} className={ (this.state.curPage == "library" ? "active-nav " : "" ) + "custom-mobile-nav-item" }><FontAwesomeIcon className="sidebar-icon" icon={faBook}></FontAwesomeIcon> Library</div>
                </li>
                <li class="nav-item">
                  <div onClick={()=>this.switchWindows("videos")} className={ (this.state.curPage == "videos" ? "active-nav " : "" ) + "custom-mobile-nav-item" }><FontAwesomeIcon className="sidebar-icon" icon={faVideo}></FontAwesomeIcon> Your Videos</div>
                </li>
                <li class="nav-item">
                  <div onClick={()=>this.switchWindows("messages")} className={ (this.state.curPage == "messages" ? "active-nav " : "" ) + "custom-mobile-nav-item" }><FontAwesomeIcon className="sidebar-icon" icon={faEnvelope}></FontAwesomeIcon> Messages</div>
                </li>
                <li class="nav-item">
                  <div onClick={()=>this.switchWindows("podcasts")} className={ (this.state.curPage == "podcasts" ? "active-nav " : "" ) + "custom-mobile-nav-item" }><FontAwesomeIcon className="sidebar-icon" icon={faMicrophone}></FontAwesomeIcon> Podcasts</div>
                </li>

                <div className="nav-separator"></div>

                <li class="nav-item">
                  <div onClick={()=>this.switchWindows("settings")} className={ (this.state.curPage == "settings" ? "active-nav " : "" ) + "custom-mobile-nav-item" }><FontAwesomeIcon className="sidebar-icon" icon={faCog}></FontAwesomeIcon> Settings </div>
                </li>
                <li class="nav-item">
                  <div onClick={()=>this.switchWindows("about")} className={ (this.state.curPage == "about" ? "active-nav " : "" ) + "custom-mobile-nav-item" }><FontAwesomeIcon className="sidebar-icon" icon={faQuestion}></FontAwesomeIcon> About Us</div>
                </li>
              </ul>
            </div>
          </nav>
          <div className="container">
              <div className="page-content">
                {this.getCurrentPage()}
              </div>
            </div>
        </MobileView>
      </div>
    )
  }
}
